
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'Setup',
  computed: {
    players() {
      return this.$store.state.players;
    }
  },
  methods: {
    increasePlayers() {
      this.$store.commit('increasePlayers');
    },
    decreasePlayers() {
      this.$store.commit('decreasePlayers');
    }
  }
});
